import './App.css';
import {Button} from './components/button'
import {Output} from './components/output'
import {useState} from 'react'

function App() {
  const [number,setNumber] = useState("");
  const [result,setResult] = useState("");
  

  const printNumber =(event) =>{
    // setNumber(number + value);
    // console.log(number)
    setNumber(number +event.target.value)
    console.log(number +event.target.value);
  }
  const ans= () =>{
    // let stringednumber = number.toString()
    let intans = eval(number)
    console.log(intans)
    setResult(intans)
  }
  // const displayans = ()=>{ans.intans()}

  const Clearnumber = () =>{
      setNumber("")
      setResult("")
  }

  return (

    <>
    <h1>Calculator App</h1>
    <Output result= {number}/>
    <Output result= {result}/>
    <Button fn={printNumber} label="1" value={1}/>
    <Button fn={printNumber} label="2" value={2}/>
    <Button fn={printNumber} label="3" value={3}/>
    <Button fn={printNumber} label="4" value={4}/>
    <Button fn={printNumber} label="5" value={5}/>
    <Button fn={printNumber} label="6" value={6}/>
    <Button fn={printNumber} label="7" value={7}/>
    <Button fn={printNumber} label="8" value={8}/>
    <Button fn={printNumber} label="9" value={9}/>
    <Button fn={printNumber} label="0" value={0}/>
    <Button fn={printNumber} label="+" value={"+"}/>
    <Button fn={printNumber} label="-" value={"-"} />
    <Button fn={printNumber} label="*" value={"*"} />
    <Button fn={printNumber} label="/" value={"/"} />
    <Button fn={ans} label="=" />
    <Button fn={Clearnumber} label="Clear all"/>
    
    
    



    </>
  );
}

export default App;
